<template>
  <div>
    <div class="split-line">
      <span><i class="iconfont icon-computer"></i>{{ title }}</span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{ title }}</el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp">
      <el-button type="success" plain @click="isAddProblemDrawer = true"
        ></el-button
      >
      <div>
        <el-tag type="danger"
          ><i class="el-icon-s-opportunity"></i> - {{ totalScore }}</el-tag
        >
        <el-tag><i class="el-icon-info"></i> - {{ totalNum }}</el-tag>
      </div>
    </el-row>
    <!--  -->
    <el-table :data="cProblemList" stripe style="width: 100%" max-height="600">
      <el-table-column type="index" label="#"> </el-table-column>
      <el-table-column prop="title" label="" min-width="300">
      </el-table-column>
      <el-table-column prop="questionType" label="" min-width="100">
        <template v-slot="{ row }">
          <span v-if="row.questionType === 0"></span>
          <span v-else-if="row.questionType === 1"></span>
          <span v-else-if="row.questionType === 2"></span>
          <span v-else-if="row.questionType === 3"></span>
        </template>
      </el-table-column>
      <el-table-column prop="difficulty" label="" min-width="100">
        <template v-slot="{ row }">
          <el-rate v-model="row.difficulty" disabled> </el-rate>
        </template>
      </el-table-column>
      <el-table-column prop="score" label="" min-width="100">
      </el-table-column>
      <el-table-column label="" min-width="150">
        <template v-slot="{ row }">
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            @click="showEditProblemDrawer(row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteProblemDialog(row.id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-drawer
      title=""
      :visible.sync="isAddProblemDrawer"
      direction="rtl"
      ref="addProblemDrawer"
      class="myDrawer"
      :wrapperClosable="false"
      @close="closeDrawer('addProblemForm')"
    >
      <div class="demo-drawer__content">
        <el-form
          :model="addProblemForm"
          :rules="problemRules"
          ref="addProblemForm"
          label-width="100px"
          size="medium"
          hide-required-asterisk
        >
          <el-form-item label="">
            <el-select
              placeholder=""
              v-model="addProblemForm.subjectId"
            >
              <el-option
                v-for="(val, key) in miniSubjects"
                :key="key"
                :value="key"
                :label="val"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title"
            ><el-input v-model.trim="addProblemForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content"
            ><el-input
              type="textarea"
              v-model.trim="addProblemForm.content"
              :rows="4"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="questionType">
            <el-select
              placeholder=""
              v-model="addProblemForm.questionType"
            >
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
            </el-select>
          </el-form-item>
          <!--  v-if -->
          <div v-if="isShowAddSelect">
            <el-form-item label="A" prop="optionA"
              ><el-input v-model="addProblemForm.optionA"></el-input>
            </el-form-item>
            <el-form-item label="B" prop="optionB"
              ><el-input v-model="addProblemForm.optionB"></el-input>
            </el-form-item>
            <el-form-item label="C" prop="optionC"
              ><el-input v-model="addProblemForm.optionC"></el-input>
            </el-form-item>
            <el-form-item label="D" prop="optionD"
              ><el-input v-model="addProblemForm.optionD"></el-input>
            </el-form-item>
          </div>
          <el-form-item label="" prop="answer">
            <el-input
              type="textarea"
              :rows="4"
              v-model="addProblemForm.answer"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="parse">
            <el-input
              type="textarea"
              :rows="4"
              v-model="addProblemForm.parse"
            ></el-input
          ></el-form-item>
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="addProblemForm.difficulty">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="2"></el-radio-button>
              <el-radio-button label="3"></el-radio-button>
              <el-radio-button label="4"></el-radio-button>
              <el-radio-button label="5"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="score"
            ><el-input
              v-model.number="addProblemForm.score"
              type="number"
              min="1"
              max="50"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer btns">
          <el-button type="primary" @click="isAddProblemDrawer = false"
            ></el-button
          >
          <el-button type="success" @click="submitForm('addProblemForm')"
            ></el-button
          >
        </div>
      </div>
    </el-drawer>
    <!--  -->
    <el-drawer
      title=""
      :visible.sync="isEditProblemDrawer"
      direction="ltr"
      ref="editProblemDrawer"
      class="myDrawer"
      :wrapperClosable="false"
      @close="closeDrawer('editProblemForm')"
    >
      <div class="demo-drawer__content">
        <el-form
          :model="editProblemForm"
          :rules="problemRules"
          ref="editProblemForm"
          label-width="100px"
          size="medium"
          hide-required-asterisk
        >
          <el-form-item label="">
            <el-select
              placeholder=""
              v-model="editProblemForm.subjectId"
            >
              <el-option
                v-for="(val, key) in miniSubjects"
                :key="key"
                :value="Number(key)"
                :label="val"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title"
            ><el-input v-model.trim="editProblemForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content"
            ><el-input
              type="textarea"
              v-model.trim="editProblemForm.content"
              :rows="4"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="questionType">
            <el-select
              placeholder=""
              v-model="editProblemForm.questionType"
            >
              <el-option label="" :value="0"></el-option>
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <!--  v-if -->
          <div v-if="isShowAddSelect">
            <el-form-item label="A" prop="optionA"
              ><el-input v-model="editProblemForm.optionA"></el-input>
            </el-form-item>
            <el-form-item label="B" prop="optionB"
              ><el-input v-model="editProblemForm.optionB"></el-input>
            </el-form-item>
            <el-form-item label="C" prop="optionC"
              ><el-input v-model="editProblemForm.optionC"></el-input>
            </el-form-item>
            <el-form-item label="D" prop="optionD"
              ><el-input v-model="editProblemForm.optionD"></el-input>
            </el-form-item>
          </div>
          <el-form-item label="" prop="answer">
            <el-input
              type="textarea"
              :rows="4"
              v-model="editProblemForm.answer"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="parse">
            <el-input
              type="textarea"
              :rows="4"
              v-model="editProblemForm.parse"
            ></el-input
          ></el-form-item>
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="editProblemForm.difficulty">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="2"></el-radio-button>
              <el-radio-button label="3"></el-radio-button>
              <el-radio-button label="4"></el-radio-button>
              <el-radio-button label="5"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="score"
            ><el-input
              v-model.number="editProblemForm.score"
              type="number"
              min="1"
              max="50"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer btns">
          <el-button type="primary" @click="isEditProblemDrawer = false"
            ></el-button
          >
          <el-button type="success" @click="submitForm('editProblemForm')"
            ></el-button
          >
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  props: ['id', 'title'],
  data() {
    var checkScore = function (rule, value, callback) {
      if (!Number.isInteger(value)) {
        callback(new Error(''))
      } else {
        if (value < 0) {
          callback(new Error('0'))
        } else if (value > 50) {
          callback(new Error('50'))
        } else {
          callback()
        }
      }
    }
    return {
      cProblemList: [],
      addProblemForm: {},
      editProblemForm: {},
      isAddProblemDrawer: false,
      isEditProblemDrawer: false,
      miniSubjects: {},
      // 
      problemRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 50, message: ' 3  50 ', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
        questionType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        optionA: [
          { required: true, message: 'A', trigger: 'blur' }
        ],
        optionB: [
          { required: true, message: 'B', trigger: 'blur' }
        ],
        optionC: [
          { required: true, message: 'C', trigger: 'blur' }
        ],
        optionD: [
          { required: true, message: 'D', trigger: 'blur' }
        ],
        answer: [
          { required: true, message: '', trigger: 'blur' }
        ],
        difficulty: [
          { required: true, message: '', trigger: 'blur' }
        ],
        score: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkScore, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    async getProblemById() {
      const { status, data } = await this.$http.get(
        '/question/api/getQuestionsByContestId/',
        {
          params: { contestId: this.id }
        }
      )
      if (status === 200) {
        this.cProblemList = data
      } else {
        this.$message.warning('')
      }
    },
    // 
    async getSubject() {
      const size = sessionStorage.getItem('total') || 30
      const { data, status } = await this.$http.get(
        '/subject/api/pageSubjects',
        {
          params: {
            size
          }
        }
      )
      if (status === 200) {
        data.list.map(({ name, id }) => {
          this.miniSubjects[id] = name
        })
      } else {
        this.$message.error('')
      }
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid) return
        if (formName === 'editProblemForm') {
          this.handleSubmit(
            '/question/api/updateQuestion',
            this.editProblemForm,
            1
          )
        } else if (formName === 'addProblemForm') {
          this.handleSubmit('/question/api/addQuestion', this.addProblemForm, 0)
        }
      })
    },
    //  0- 1-
    async handleSubmit(url, formData, flag) {
      Object.assign(formData, { contestId: this.id })
      const { data, status } = await this.$http.post(url, formData)
      if (status === 200) {
        if (data.success) {
          if (flag === 0) {
            // 
            this.$message.success('')
            this.isAddProblemDrawer = false
          } else {
            this.$message.success('')
            this.isEditProblemDrawer = false
          }
          this.getProblemById()
        } else this.$message.error(`${flag ? '' : ''}`)
      } else {
        this.$message.warning('')
      }
    },
    // 
    closeDrawer(formName) {
      this.$refs[formName].resetFields()
    },
    // 
    showEditProblemDrawer(data) {
      const temp = this.toConvert(data)
      this.editProblemForm = temp
      this.isEditProblemDrawer = true
    },
    // 
    async deleteProblemDialog(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { status, data } = await this.$http.delete(
            '/question/api/deleteQuestion/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.getProblemById()
            } else this.$message.error('')
          } else {
            this.$message.warning('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    }
  },
  computed: {
    // 
    totalScore() {
      let temp = 0
      this.cProblemList.forEach((item) => {
        temp += item.score
      })
      return temp
    },
    // 
    totalNum() {
      return this.cProblemList.length
    },
    // 
    isShowAddSelect() {
      return (
        this.addProblemForm.questionType === '0' ||
        this.addProblemForm.questionType === '1'
      )
    },
    // 
    isShowEditSelect() {
      return (
        this.editProblemForm.questionType === 0 ||
        this.editProblemForm.questionType === 1
      )
    }
  },
  created() {
    this.getSubject()
    this.getProblemById()
  }
}
</script>
<style lang="less" scoped>
.showInfo {
  background-color: #fff;
}

.mixInp {
  & > div {
    float: right;
    .el-tag {
      display: inline-block;
      height: 40px;
      font-size: 16px;
      margin-right: 20px;
      letter-spacing: 0.2em;
      i {
        margin-right: 4px;
        height: 40px;
        line-height: 40px;
      }
    }
  }
}
</style>
