<template>
  <div>
    <div class="split-line">
      <span><i class="icon-book iconfont"></i></span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          class="input-with-select"
          v-model="query.keyword"
          clearable
          @clear="getSubject()"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getSubject()"
          ></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="success" plain @click="isAddSubjectDiaglog = true"
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table stripe style="width: 100%" max-height="600" :data="subjectList">
      <el-table-column label="#" prop="id" min-width="40"> </el-table-column>
      <el-table-column label="" prop="name" min-width="100">
      </el-table-column>
      <el-table-column label="" prop="createTime" min-width="150">
        <template v-slot:default="{ row }">
          {{ row.createTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column label="" prop="updateTime" min-width="150">
        <template v-slot:default="{ row }">
          {{ row.updateTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column label="" prop="questionNum" min-width="80">
        <template v-slot:default="scope">
          <el-tag
            effect="plain"
            type="info"
            :class="scope.row.questionNum > 0 ? 'myNum' : ''"
          >
            {{ scope.row.questionNum }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template v-slot="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            @click="editSubjectDialog(scope.row.id)"
            circle
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            @click="deleteSubjectDialog(scope.row.id)"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <!--  -->
    <el-dialog
      :visible.sync="isAddSubjectDiaglog"
      width="20%"
      @close="closeSubjectDialog"
    >
      <el-form
        :model="addSubjectForm"
        ref="addSubjectForm"
        :rules="addSubjectRules"
        :hide-required-asterisk="true"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addSubjectForm.name"></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isAddSubjectDiaglog = false"> </el-button>
        <el-button type="primary" @click="submitAddSubject()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="isEditSubjectDiaglog" width="20%" center>
      <el-form
        :model="editSubjectForm"
        ref="editSubjectForm"
        :rules="editSubjectRules"
        :hide-required-asterisk="true"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="editSubjectForm.name"></el-input>
        </el-form-item>
        <el-form-item prop="imgUrl" label-width="0">
          <el-upload
            class="avatar-uploader"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            action="http://127.0.0.1:8088/uploadfile"
            name="files"
          >
            <img :src="bindSrc(editSubjectForm.imgUrl)" class="avatar" />
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isEditSubjectDiaglog = false"> </el-button>
        <el-button type="primary" @click="submitEditSubject()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      subjectList: [],
      query: {
        keyword: null,
        page: 1,
        size: 10
      },
      total: 10,
      isAddSubjectDiaglog: false,
      isEditSubjectDiaglog: false,
      addSubjectForm: {},
      addSubjectRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ]
      },
      editSubjectForm: {},
      editSubjectRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    async getSubject() {
      const { data, status } = await this.$http.get(
        '/subject/api/pageSubjects',
        {
          params: this.query
        }
      )
      if (status === 200) {
        const { list, pageSize: size, total } = data
        this.subjectList = list
        this.total = total
        Object.assign(this.query, size)
        window.sessionStorage.setItem('total', total)
      }
    },
    // 
    handleSizeChange(size) {
      this.query.size = size
      this.query.page = 1
      this.getSubject()
    },
    // 
    handleCurrentChange(currentPage) {
      this.query.page = currentPage
      this.getSubject()
    },
    // 
    submitAddSubject() {
      this.$refs.addSubjectForm.validate(async (valid) => {
        if (!valid) return false
        const formData = this.addSubjectForm
        const { data, status } = await this.$http.post(
          '/subject/api/addSubject',
          formData
        )
        if (status === 200) {
          if (data.success) {
            this.$message.success('')
            this.isAddSubjectDiaglog = false
          } else {
            this.$message.error('')
          }
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    closeSubjectDialog() {
      this.isAddSubjectDiaglog = false
      this.$refs.addSubjectForm.resetFields()
    },
    // 
    deleteSubjectDialog(id) {
      console.log(id)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { data, status } = await this.$http.delete(
            '/subject/api/deleteSubject/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.getSubject()
            } else {
              this.$message.error('')
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    },
    // 
    async editSubjectDialog(id) {
      this.isEditSubjectDiaglog = true
      const { data, status } = await this.$http.get(
        '/subject/api/getSubjectById/' + id
      )
      if (status === 200) {
        this.editSubjectForm = data
      }
    },
    // 
    submitEditSubject() {
      this.isEditSubjectDiaglog = false
      this.$refs.editSubjectForm.validate(async (valid) => {
        if (!valid) return false
        // const res = await this.$http.get(
        //   '/question/api/getQuestionsBySubjectId',
        //   {
        //     params: { subjectId: this.editSubjectForm.id }
        //   }
        // )
        // this.editSubjectForm.questionNum = res.data
        const { data, status } = await this.$http.post(
          '/subject/api/updateSubject',
          this.editSubjectForm
        )
        if (status === 200) {
          if (data.success) {
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    handleAvatarSuccess(res, file) {
      this.editSubjectForm.imgUrl = file.raw.name
    }
  },
  created() {
    this.getSubject()
  }
}
</script>
