<template>
  <div v-highlight>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-divider></el-divider>
    <el-alert
      type="warning"
      title=",,,,"
      show-icon
      center
      class="postAlert"
    >
    </el-alert>
    <el-input placeholder="" v-model="title" class="postTitle">
      <template slot="prepend"></template>
    </el-input>
    <quill-editor ref="myQuillEditor" v-model="content" :options="options" />

    <el-button class="submitPost" type="danger" @click="getContent" plain>
      
    </el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: '',
      title: '',
      user: {},
      options: {
        theme: 'snow',
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'], // 
            ['blockquote', 'code-block'], // 
            [{ header: 1 }, { header: 2 }], // 
            [{ list: 'ordered' }, { list: 'bullet' }], // 
            [{ script: 'sub' }, { script: 'super' }], // 
            [{ indent: '-1' }, { indent: '+1' }], // 
            // [{ direction: "rtl" }], // 
            [{ size: ['small', false, 'large', 'huge'] }], // 
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
            [{ color: [] }, { background: [] }], // 
            [{ font: [] }], // 
            [{ align: [] }], // 
            ['clean'], // 
            [('link', 'image', 'video')] // 
          ]
        }
      }
    }
  },
  methods: {
    onEditorBlur({ html, text }) {
      console.log(html, text)
    },
    async getContent() {
      const temp = {
        textContent: this.editor.getText().trim(),
        htmlContent: this.content.trim(),
        authorId: this.user.id,
        title: this.title.trim()
      }
      if (this.validate(temp)) {
        const { data, status } = await this.$http.post(
          '/post/api/addPost',
          temp
        )
        if (status === 200) {
          if (data.success) {
            this.$message.success('')
            this.title = ''
            this.content = ''
            window.history.go(-1)
          }
        } else {
          this.$message.error('')
        }
      }
    },
    // 
    validate(data) {
      if (data.title.length === 0) {
        this.$notify.error({
          title: '',
          dangerouslyUseHTMLString: true,
          message: '<strong style="color:red;"></strong>'
        })
        return false
      }
      if (data.textContent.length === 0) {
        this.$notify.error({
          title: '',
          dangerouslyUseHTMLString: true,
          message: '<strong style="color:red;"></strong>'
        })
        return false
      }
      return true
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    }
  },
  created() {
    this.user = this.$store.state.currentUser
  }
}
</script>
<style lang="less" scoped>
.el-alert {
  margin: 20px 0;
  .el-alert__description {
    font-size: 20px;
  }
}

.el-alert .el-alert__description {
  font-size: 20px;
}

.submitPost {
  float: right;
  margin: 20px;
}
</style>

<style lang="less">
.postAlert {
  .el-alert__title {
    font-size: 15px;
  }
}
.postTitle {
  border-radius: 6px;
  margin-bottom: 20px;
  outline: none;
}
.postTitle.el-input.el-input-group.el-input-group--prepend {
  width: 500px;
}
</style>

<style lang="less">
</style>
