<template>
  <div>
    <div class="split-line">
      <span> <i class="icon-user1 iconfont"></i> </span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          class="input-with-select"
          v-model="query.keyword"
          clearable
          @clear="getProblem()"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getProblem()"
          ></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="success" @click="showAddDrawer" plain
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="problemList" stripe style="width: 100%" max-height="600">
      <el-table-column prop="id" label="" min-width="60"></el-table-column>
      <el-table-column
        prop="title"
        label=""
        min-width="300"
      ></el-table-column>
      <el-table-column prop="sujectId" label="" min-width="100">
        <template v-slot="{ row }">
          {{ miniSubjects[row.subjectId] }}
        </template>
      </el-table-column>
      <el-table-column prop="questionType" label="" min-width="100">
        <template v-slot="{ row }">
          <span v-if="row.questionType === 0"></span>
          <span v-else-if="row.questionType === 1"></span>
          <span v-else-if="row.questionType === 2"></span>
          <span v-else-if="row.questionType === 3"></span>
        </template>
      </el-table-column>
      <el-table-column prop="difficulty" label="" min-width="100">
        <template v-slot="{ row }">
          <el-rate v-model="row.difficulty" disabled> </el-rate>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="100">
        <template v-slot="{ row }">
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            @click="editProblemDrawer(row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteProblemDialog(row.id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <!--  -->
    <el-drawer
      title=""
      :visible.sync="isAddProblemDrawer"
      direction="rtl"
      ref="addProblemDrawer"
      class="myDrawer"
      :wrapperClosable="false"
      @close="closeDrawer('addProblemForm')"
    >
      <div class="demo-drawer__content">
        <el-form
          :model="addProblemForm"
          :rules="problemRules"
          ref="addProblemForm"
          label-width="100px"
          size="medium"
          hide-required-asterisk
        >
          <el-form-item label="">
            <el-select
              placeholder=""
              v-model="addProblemForm.subjectId"
            >
              <el-option
                v-for="(val, key) in miniSubjects"
                :key="key"
                :value="key"
                :label="val"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title"
            ><el-input v-model.trim="addProblemForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content"
            ><el-input
              type="textarea"
              v-model.trim="addProblemForm.content"
              :rows="4"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="questionType">
            <el-select
              placeholder=""
              v-model="addProblemForm.questionType"
            >
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
            </el-select>
          </el-form-item>
          <!--  v-if -->
          <div v-if="isShowAddSelect">
            <el-form-item label="A" prop="optionA"
              ><el-input v-model="addProblemForm.optionA"></el-input>
            </el-form-item>
            <el-form-item label="B" prop="optionB"
              ><el-input v-model="addProblemForm.optionB"></el-input>
            </el-form-item>
            <el-form-item label="C" prop="optionC"
              ><el-input v-model="addProblemForm.optionC"></el-input>
            </el-form-item>
            <el-form-item label="D" prop="optionD"
              ><el-input v-model="addProblemForm.optionD"></el-input>
            </el-form-item>
          </div>
          <el-form-item label="" prop="answer">
            <el-input
              type="textarea"
              :rows="4"
              v-model="addProblemForm.answer"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="parse">
            <el-input
              type="textarea"
              :rows="4"
              v-model="addProblemForm.parse"
            ></el-input
          ></el-form-item>
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="addProblemForm.difficulty">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="2"></el-radio-button>
              <el-radio-button label="3"></el-radio-button>
              <el-radio-button label="4"></el-radio-button>
              <el-radio-button label="5"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="score"
            ><el-input
              v-model.number="addProblemForm.score"
              type="number"
              min="1"
              max="50"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer btns">
          <el-button type="primary" @click="isAddProblemDrawer = false"
            ></el-button
          >
          <el-button type="success" @click="submitAddForm"></el-button>
        </div>
      </div>
    </el-drawer>
    <!--  -->
    <el-drawer
      title=""
      :visible.sync="isEditProblemDrawer"
      direction="ltr"
      ref="editProblemDrawer"
      class="myDrawer"
      :wrapperClosable="false"
      @close="closeDrawer('editProblemForm')"
    >
      <div class="demo-drawer__content">
        <el-form
          :model="editProblemForm"
          :rules="problemRules"
          ref="editProblemForm"
          label-width="100px"
          size="medium"
          hide-required-asterisk
        >
          <el-form-item label="">
            <el-select
              placeholder=""
              v-model="editProblemForm.subjectId"
            >
              <el-option
                v-for="(val, key) in miniSubjects"
                :key="key"
                :value="Number(key)"
                :label="val"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title"
            ><el-input v-model.trim="editProblemForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content"
            ><el-input
              type="textarea"
              v-model.trim="editProblemForm.content"
              :rows="4"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="questionType">
            <el-select
              placeholder=""
              v-model="editProblemForm.questionType"
            >
              <el-option label="" :value="0"></el-option>
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <!--  v-if -->
          <div v-if="isShowEditSelect">
            <el-form-item label="A" prop="optionA"
              ><el-input v-model="editProblemForm.optionA"></el-input>
            </el-form-item>
            <el-form-item label="B" prop="optionB"
              ><el-input v-model="editProblemForm.optionB"></el-input>
            </el-form-item>
            <el-form-item label="C" prop="optionC"
              ><el-input v-model="editProblemForm.optionC"></el-input>
            </el-form-item>
            <el-form-item label="D" prop="optionD"
              ><el-input v-model="editProblemForm.optionD"></el-input>
            </el-form-item>
          </div>
          <el-form-item label="" prop="answer">
            <el-input
              type="textarea"
              :rows="4"
              v-model="editProblemForm.answer"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="parse">
            <el-input
              type="textarea"
              :rows="4"
              v-model="editProblemForm.parse"
            ></el-input
          ></el-form-item>
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="editProblemForm.difficulty">
              <el-radio-button label="1"></el-radio-button>
              <el-radio-button label="2"></el-radio-button>
              <el-radio-button label="3"></el-radio-button>
              <el-radio-button label="4"></el-radio-button>
              <el-radio-button label="5"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="score"
            ><el-input
              v-model.number="editProblemForm.score"
              type="number"
              min="1"
              max="50"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer btns">
          <el-button type="primary" @click="isEditProblemDrawer = false"
            ></el-button
          >
          <el-button type="success" @click="submitEditForm"></el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    var checkScore = function (rule, value, callback) {
      if (!Number.isInteger(value)) {
        callback(new Error(''))
      } else {
        if (value < 0) {
          callback(new Error('0'))
        } else if (value > 50) {
          callback(new Error('50'))
        } else {
          callback()
        }
      }
    }
    return {
      problemList: [],
      query: {
        page: 1,
        size: 10,
        keyword: null
      },
      total: 0,
      isAddProblemDrawer: false,
      addProblemForm: {},
      // 
      problemRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 50, message: ' 3  50 ', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
        questionType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        optionA: [
          { required: true, message: 'A', trigger: 'blur' }
        ],
        optionB: [
          { required: true, message: 'B', trigger: 'blur' }
        ],
        optionC: [
          { required: true, message: 'C', trigger: 'blur' }
        ],
        optionD: [
          { required: true, message: 'D', trigger: 'blur' }
        ],
        answer: [
          { required: true, message: '', trigger: 'blur' }
        ],
        difficulty: [
          { required: true, message: '', trigger: 'blur' }
        ],
        score: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkScore, trigger: 'blur' }
        ]
      },
      // 
      miniSubjects: {},
      isEditProblemDrawer: false,
      editProblemForm: {}
    }
  },
  methods: {
    // 
    async getProblem() {
      const { data, status } = await this.$http.get(
        '/question/api/pageQuestion',
        {
          params: this.query
        }
      )
      if (status === 200) {
        const { list, pageNum: page, total } = data
        this.problemList = list
        Object.assign(this.query, page)
        this.total = total
      } else {
        this.$message.warning('')
      }
    },
    // 
    showAddDrawer() {
      this.isAddProblemDrawer = true
    },
    // 
    submitAddForm() {
      this.$refs.addProblemForm.validate(async (valid) => {
        if (!valid) return
        const { data, status } = await this.$http.post(
          '/question/api/addQuestion',
          this.addProblemForm
        )
        if (status) {
          if (data.success) {
            this.isAddProblemDrawer = false
            this.getProblem()
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        } else {
          this.$message.warning('')
        }
      })
    },
    // 
    async getSubject() {
      const size = sessionStorage.getItem('total') || 30
      const { data, status } = await this.$http.get(
        '/subject/api/pageSubjects',
        {
          params: {
            size
          }
        }
      )
      if (status === 200) {
        data.list.map(({ name, id }) => {
          this.miniSubjects[id] = name
        })
      } else {
        this.$message.error('')
      }
    },
    // 
    handleSizeChange(size) {
      this.query.size = size
      this.query.page = 1
      this.getProblem()
    },
    // 
    handleCurrentChange(currentIndex) {
      this.query.page = currentIndex
      this.getProblem()
    },
    // 
    deleteProblemDialog(id) {
      console.log(id)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { data, status } = await this.$http.delete(
            '/question/api/deleteQuestion/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.isAddProblemDrawer = false
              this.getProblem()
            } else {
              this.$message.error('')
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    },
    // 
    editProblemDrawer(data) {
      this.isEditProblemDrawer = true
      this.editProblemForm = this.toConvert(data)
    },
    // 
    submitEditForm() {
      this.$refs.editProblemForm.validate(async (valid) => {
        if (!valid) return
        console.log(this.editProblemForm)
        const success = await this.$http.post(
          '/question/api/updateQuestion',
          this.editProblemForm
        )
        if (success) {
          this.$message.success('')
          this.isEditProblemDrawer = false
          this.getProblem()
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    closeDrawer(formName) {
      this.$refs[formName].resetFields()
    }
  },
  computed: {
    isShowAddSelect() {
      return (
        this.addProblemForm.questionType === '0' ||
        this.addProblemForm.questionType === '1'
      )
    },
    isShowEditSelect() {
      return (
        this.editProblemForm.questionType === 0 ||
        this.editProblemForm.questionType === 1
      )
    }
  },
  created() {
    this.getSubject()
    this.getProblem()
  }
}
</script>

<style lang="less">
/*  */
.el-drawer.rtl,
.el-drawer.ltr {
  overflow: auto !important;
}

/*  */
.el-drawer__container ::-webkit-scrollbar {
  display: none;
}

.myDrawer {
  .el-form {
    padding-right: 10%;
    .el-form-item {
      .el-select {
        width: 100%;
      }
    }
  }
  .btns {
    margin-bottom: 16px;
    text-align: center;
  }
}
</style>
