<template>
  <div>
    <div class="split-line">
      <span><i class="icon-comment iconfont"></i></span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-table :data="commentList" stripe style="width: 100%" max-height="600">
      <el-table-column prop="content" label="" min-width="200">
      </el-table-column>
      <el-table-column prop="userId" label="" min-width="150">
        <template v-slot="{ row }">
          {{ getUserNameById(row.userId) }}
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="" min-width="150">
        <template v-slot="{ row }">
          {{ row.createTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column label="" min-width="100">
        <template v-slot="{ row }">
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteCommentById(row.id)"
          >
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.pageNum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      commentList: [],
      query: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0
    }
  },
  methods: {
    // 
    async getComment() {
      const { status, data } = await this.$http.get(
        '/comment/api/pageComment',
        {
          params: this.query
        }
      )
      if (status === 200) {
        const { comments, totalPageSize } = data
        this.total = totalPageSize
        this.commentList = comments
      } else {
        this.$message.warning('')
      }
    },
    // 
    handleSizeChange(size) {
      this.query.pageSize = size
      this.query.pageNum = 1
      this.getComment()
    },
    // 
    handleCurrentChange(currentIndex) {
      this.query.pageNum = currentIndex
      this.getComment()
    },
    // 
    getUserNameById(id) {
      return this.getUserById(id) && this.getUserById(id).name
    },
    async deleteCommentById(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { data, status } = await this.$http.delete(
            '/comment/api/deleteComment/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.getComment()
            } else {
              this.$message.error('')
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    }
  },
  created() {
    this.getComment()
  },
  computed: {
    ...mapGetters(['getUserById'])
  }
}
</script>
<style lang="less" scoped>
.el-breadcrumb {
  margin-bottom: 20px;
}
</style>
