<template>
  <div>
    <div class="split-line">
      <span><i class="icon-chart iconfont"></i></span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          class="input-with-select"
          v-model="query.keyword"
          clearable
          @clear="getContest()"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getContest()"
          ></el-button>
        </el-input>
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="contestList" stripe style="width: 100%" max-height="600">
      <el-table-column prop="title" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="startTime" label="" min-width="150">
        <template v-slot="{ row }">
          <!-- {{ formatDate(scope.row.startTime) }} -->
          {{ row.startTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="endTime" label="" min-width="150">
        <template v-slot="{ row }">
          <!-- {{ formatDate(scope.row.endTime) }} -->
          {{ row.endTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="subjectId" label="" min-width="120">
        <template v-slot:default="{ row }">
          {{ findSubJectById(row.subjectId) }}
        </template>
      </el-table-column>
      <el-table-column prop="state" label="" min-width="100">
        <template v-slot="{ row }">
          <el-tag effect="dark" type="success" v-if="row.state === 1"
            >{{ '' }}
          </el-tag>
          <el-tag effect="dark" type="warning" v-else-if="row.state === 2"
            >{{ '' }}
          </el-tag>
          <el-tag effect="dark" type="danger" v-else-if="row.state === 3"
            >{{ '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="200">
        <template v-slot="{ row }">
          <el-button
            :disabled="row.state !== 3"
            type="success"
            icon="el-icon-search"
            circle
            @click="goShowGrade(row.id, row.title)"
          ></el-button>
          <el-button
            :disabled="row.state === 3"
            type="primary"
            circle
            @click="goCorrectGrade(row.id, row.title)"
            ><i class="el-icon-edit"></i
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contestList: [],
      miniSubjects: {},
      query: {
        keyword: null,
        page: 1,
        size: 10
      },
      total: 10
    }
  },
  methods: {
    // 
    findSubJectById(id) {
      return this.miniSubjects[id] || ''
    },
    // 
    async getSubject() {
      const size = sessionStorage.getItem('total') || 30
      const { data, status } = await this.$http.get(
        '/subject/api/pageSubjects',
        {
          params: {
            size
          }
        }
      )
      if (status === 200) {
        data.list.map(({ name, id }) => {
          this.miniSubjects[id] = name
        })
      } else {
        this.$message.error('')
      }
    },
    // 
    async getContest() {
      const { data, status } = await this.$http.get(
        '/contest/api/pageContest',
        {
          params: this.query
        }
      )
      if (status === 200) {
        const { total, list, pageSize: size } = data
        this.contestList = list
        Object.assign(this.query, size)
        this.total = total
      }
    },
    // 
    handleSizeChange(size) {
      this.query.size = size
      this.query.page = 1
      this.getContest()
    },
    // 
    handleCurrentChange(currentPage) {
      this.query.page = currentPage
      this.getContest()
    },
    goShowGrade(id, title) {
      this.$router.push(`/_grade/show/${id}/${title}`)
    },
    goCorrectGrade(id, title) {
      this.$router.push(`/_grade/correct/${id}/${title}`)
    }
  },
  created() {
    this.getSubject()
    this.getContest()
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
</style>
