<template>
  <div>
    <div class="split-line">
      <span> <i class="icon-user1 iconfont"></i> </span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          v-model="search"
          class="input-with-select"
          clearable
          @clear="getUsers()"
        >
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="success" @click="addUserDialog()" plain
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="userList" stripe style="width: 100%" max-height="600">
      <el-table-column prop="avatarImgUrl" label="">
        <template v-slot:default="scope">
          <el-avatar
            shape="square"
            :size="60"
            :src="bindSrc(scope.row.avatarImgUrl)"
          ></el-avatar>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" min-width="100">
      </el-table-column>
      <el-table-column prop="username" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="qq" label="QQ" min-width="120"> </el-table-column>
      <el-table-column prop="phone" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="email" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="level" label="" min-width="80">
        <template v-slot:default="scope">
          <el-tag
            :type="['', 'success', 'danger'][scope.row.level]"
            effect="dark"
            >{{ levels[scope.row.level] }}</el-tag
          >
        </template>
      </el-table-column>
      <el-table-column prop="state" label="" min-width="80">
        <template v-slot:default="scope">
          <el-switch
            v-model="scope.row.state"
            active-color="#13ce66"
            inactive-color="#ff4949"
            :active-value="0"
            :inactive-value="1"
            active-text=""
            inactive-text=""
            @change="userStateChange(scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="100">
        <template v-slot:default="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            @click="editUserDialog(scope.row.id)"
            circle
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            @click="deleteUserDialog(scope.row.id)"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      :visible.sync="isAddUserDialog"
      width="30%"
      @close="closeUserDialog"
    >
      <el-form
        :model="addUserFormData"
        :rules="addUserRules"
        ref="addUserForm"
        label-width="80px"
        :hide-required-asterisk="true"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addUserFormData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="addUserFormData.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="addUserFormData.password"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item label="qq" prop="qq">
          <el-input v-model="addUserFormData.qq"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="addUserFormData.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserFormData.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="level">
          <el-select v-model="addUserFormData.level" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isAddUserDialog = false"> </el-button>
        <el-button type="primary" @click="submitAddUser('addUserForm')"
          > </el-button
        >
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="isEditUserDialog" width="30%">
      <el-form
        :model="editUserFormData"
        :rules="editUserRules"
        ref="editUserForm"
        label-width="80px"
      >
        <!--  -->
        <el-form-item label-width="0">
          <el-upload
            class="avatar-uploader"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :action="bindSrc('uploadfile')"
            name="files"
          >
            <img :src="bindSrc(editUserFormData.avatarImgUrl)" class="avatar" />
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editUserFormData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="editUserFormData.username"></el-input>
        </el-form-item>
        <el-form-item label="qq" prop="qq">
          <el-input v-model="editUserFormData.qq"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editUserFormData.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUserFormData.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="level">
          <el-select v-model="editUserFormData.level" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isEditUserDialog = false"> </el-button>
        <el-button type="primary" @click="submitEditUser()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) return callback()
      callback(new Error(''))
    }
    var checkPhone = (rule, value, callback) => {
      const regPhone = /^1[34578]\d{9}$/
      if (regPhone.test(value)) return callback()
      callback(new Error(''))
    }
    //
    return {
      userList: [],
      bans: ['icon-xianxing-qiyong iconfont', 'icon-ban iconfont'],
      bansIcon: ['noban-icon', 'ban-icon'],
      levels: ['', '', ''],
      query: {
        page: 1, // 
        size: 10 // 
      },
      total: 1,
      search: '',
      isAddUserDialog: false,
      isEditUserDialog: false,
      addUserFormData: {},
      addUserRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qq: [{ required: true, message: 'qq', trigger: 'blur' }],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        level: [{ required: true, message: '', trigger: 'blur' }]
      },
      editUserFormData: {},
      editUserRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        qq: [{ required: true, message: 'qq', trigger: 'blur' }],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        level: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    async getUsers() {
      const { data, status } = await this.$http.post(
        'account/pageAccount/',
        this.query
      )
      const { list, pageNum: page, pageSize: size, total } = data
      if (status === 200) {
        this.userList = list
        this.query = { page, size }
        this.total = total
      }
    },
    // 
    handleSizeChange(newSize) {
      this.query.size = newSize
      this.query.page = 1
      this.getUsers()
    },
    // 
    handleCurrentChange(newSize) {
      this.query.page = newSize
      this.getUsers()
    },
    // 
    async userStateChange(userInfo) {
      const { id, state } = userInfo
      const { data } = await this.$http.put('/account/updateIgnoreNull', {
        id,
        state
      })
      if (data.success === true) {
        this.$message.success(data.message)
      } else {
        userInfo.state = !userInfo.state
        this.$message.error(data.message)
      }
    },
    // 
    addUserDialog() {
      this.isAddUserDialog = !this.isAddUserDialog
    },
    // 
    submitAddUser(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const submitFormData = this.mergeUseDefault(this.addUserFormData)
          const { data } = await this.$http.post(
            '/account/insert',
            submitFormData
          )
          if (data.success) {
            this.isAddUserDialog = false
            this.$message.success(data.message)
            this.getUsers()
          } else {
            this.$message.error(data.message)
            return false
          }
        } else {
          return false
        }
      })
    },
    // 
    mergeUseDefault(form) {
      const useDefault = {
        updateTime: new Date(),
        createTime: new Date(),
        level: 0,
        state: 0,
        id: Date.now() % 999999,
        avatarImgUrl: 'headimg_placeholder.png'
      }
      return Object.assign(useDefault, form)
    },
    // 
    closeUserDialog() {
      this.$refs.addUserForm.resetFields()
      this.isAddUserDialog = false
    },
    // 
    async editUserDialog(id) {
      this.isEditUserDialog = !this.isEditUserDialog
      const { data, status } = await this.$http.get('/account/getById', {
        params: {
          id
        }
      })
      if (status === 200) {
        this.editUserFormData = data
      }
    },
    // 
    handleAvatarSuccess(res, file) {
      this.editUserFormData.avatarImgUrl = file.raw.name
    },
    // 
    submitEditUser() {
      this.$refs.editUserForm.validate(async (valid) => {
        if (!valid) return false
        const editData = this.editUserFormData
        const { data } = await this.$http.put(
          '/account/updateIgnoreNull',
          editData
        )
        if (data.success) {
          this.$message.success('')
          this.getUsers()
          this.isEditUserDialog = false
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    deleteUserDialog(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { data } = await this.$http.delete('/account/delete/' + id)
          if (data.success) {
            this.$message.success('')
            this.getUsers()
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    }
  },
  mounted() {
    this.getUsers()
  }
}
</script>

<style lang="less" scoped>
.ban-icon {
  color: red;
}
.noban-icon {
  color: #67c23a;
}

.el-form {
  padding: 0 10%;
}
.el-form-item,
.el-select {
  width: 100%;
}
</style>
<style>
/*   */
.el-table .el-switch__label.is-active {
  color: red;
}
/*  */
.el-avatar > img {
  width: 100%;
  height: 100%;
}

/*  */
.el-dialog .avatar {
  width: 178px;
  height: 178px;
  display: inline-block;
  vertical-align: middle;
}
/*  */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #ff7979;
}
</style>
