<template>
  <div>
    <div class="split-line">
      <span><i class="iconfont icon-computer"></i></span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          class="input-with-select"
          v-model="query.keyword"
          clearable
          @clear="getContest()"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getContest()"
          ></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="success" @click="addContestDialog()" plain
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="contestList" stripe style="width: 100%" max-height="600">
      <el-table-column prop="title" label="" min-width="150">
      </el-table-column>
      <el-table-column prop="startTime" label="" min-width="150">
        <template v-slot="{ row }">
          <!-- {{ formatDate(scope.row.startTime) }} -->
          {{ row.startTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="endTime" label="" min-width="150">
        <template v-slot="{ row }">
          <!-- {{ formatDate(scope.row.endTime) }} -->
          {{ row.endTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="subjectId" label="" min-width="120">
        <template v-slot:default="{ row }">
          {{ findSubJectById(row.subjectId) }}
        </template>
      </el-table-column>
      <el-table-column prop="state" label="" min-width="100">
        <template v-slot="scope">
          <el-tag
            effect="dark"
            :type="['', 'success', 'danger'][scope.row.current]"
            >{{ ['', '', ''][scope.row.current] }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="100">
        <template v-slot="{ row }">
          <el-button
            icon="el-icon-search"
            type="success"
            @click="showProblemById(row.id, row.title)"
            circle
          ></el-button>
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            @click="editContestDialog(row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteContestDialog(row.id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      :visible.sync="isAddContestDialog"
      width="40%"
      class="add-contest"
      @close="clearContestDialog"
    >
      <el-form
        label-width="80px"
        :model="addContestForm"
        ref="addContestForm"
        :rules="addContestRules"
        :hide-required-asterisk="true"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="addContestForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectId">
          <el-select
            placeholder=""
            v-model="addContestForm.subjectId"
          >
            <el-option
              v-for="(val, key) in miniSubjects"
              :key="key"
              :label="val"
              :value="key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker
            type="datetimerange"
            align="center"
            start-placeholder=""
            end-placeholder=""
            v-model="addContestForm.date"
            :default-time="['9:00:00', '12:00:00']"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isAddContestDialog = false"> </el-button>
        <el-button type="primary" @click="submitAddContest()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :visible.sync="isEditContestDialog"
      width="40%"
      class="edit-contest"
    >
      <el-form
        label-width="80px"
        :model="editContestForm"
        ref="editContestForm"
        :rules="editContestRules"
        :hide-required-asterisk="true"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="editContestForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="subjectId">
          <el-select
            placeholder=""
            v-model="editContestForm.subjectId"
          >
            <el-option
              v-for="item in subjects"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker
            type="datetimerange"
            align="center"
            start-placeholder=""
            end-placeholder=""
            v-model="editContestForm.date"
            :default-time="['9:00:00', '12:00:00']"
          >
          </el-date-picker> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isEditContestDialog = false"> </el-button>
        <el-button type="primary" @click="submitEditContest()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contestList: [],
      query: {
        keyword: null,
        page: 1,
        size: 10
      },
      total: 10,
      isAddContestDialog: false,
      isEditContestDialog: false,
      subjects: {},
      miniSubjects: {},
      addContestForm: {},
      addContestRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        subjectId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        date: [{ required: true, message: '', trigger: 'blur' }]
      },
      editContestForm: {},
      editContestRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        subjectId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        date: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    async getContest() {
      const { data, status } = await this.$http.get(
        '/contest/api/pageContest',
        {
          params: this.query
        }
      )
      if (status === 200) {
        const { total, list, pageSize: size } = data
        this.contestList = list
        this.handleContestState()
        Object.assign(this.query, size)
        this.total = total
      }
    },
    // 
    handleSizeChange(size) {
      this.query.size = size
      this.query.page = 1
      this.getContest()
    },
    // 
    handleCurrentChange(currentPage) {
      this.query.page = currentPage
      this.getContest()
    },
    // 
    async getSubject() {
      const size = sessionStorage.getItem('total') || 30
      const { data, status } = await this.$http.get(
        '/subject/api/pageSubjects',
        {
          params: {
            size
          }
        }
      )
      if (status === 200) {
        // const sMap = new Map()
        // data.list.map(({ name, id }) => {
        //   sMap.set(id, name)
        // })
        // this.subjects = sMap
        data.list.map(({ name, id }) => {
          this.miniSubjects[id] = name
        })
        this.subjects = data.list
      } else {
        this.$message.error('')
      }
    },
    // 
    addContestDialog() {
      this.isAddContestDialog = true
    },
    // 
    submitAddContest() {
      this.$refs.addContestForm.validate(async (valid) => {
        if (!valid) return false
        this.addContestForm.startTime = this.addContestForm.date[0]
        this.addContestForm.endTime = this.addContestForm.date[1]
        // BUG>> state0,1
        const { data, status } = await this.$http.post(
          '/contest/api/addContest',
          this.addContestForm
        )
        if (status === 200) {
          if (data.success) {
            this.isAddContestDialog = false
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    clearContestDialog() {
      this.$refs.addContestForm.resetFields()
    },
    // 
    findSubJectById(id) {
      return this.miniSubjects[id] || ''
      // return this.subjects.get(id) || ''
    },
    // 
    deleteContestDialog(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { data, status } = await this.$http.delete(
            '/contest/api/deleteContest/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.getContest()
            } else {
              this.$message.error('')
            }
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    },
    // 
    handleContestState() {
      this.contestList.forEach((item) => {
        if (item.startTime > Date.now()) item.current = 0
        else if (item.endTime < Date.now()) item.current = 2
        else item.current = 1
      })
    },
    // 
    editContestDialog(data) {
      this.isEditContestDialog = true
      // 
      this.editContestForm = this.toConvert(data)
      this.editContestForm.date = [data.startTime, data.endTime]
    },
    // 
    submitEditContest() {
      this.$refs.editContestForm.validate(async (valid) => {
        if (!valid) return false
        const obj = {
          startTime: this.editContestForm.date[0],
          endTime: this.editContestForm.date[1],
          updateTime: Date.now()
        }
        Object.assign(this.editContestForm, obj)
        const { data, status } = await this.$http.put(
          '/contest/api/updateContest',
          this.editContestForm
        )
        if (status === 200) {
          if (data.success) {
            this.$message.success('')
            this.isEditContestDialog = false
            this.getContest()
          } else this.$message.error('')
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    showProblemById(id, title) {
      this.$router.push(`/_contest/problems/${id}/${title}`)
    }
  },
  created() {
    this.getSubject()
  },
  mounted() {
    this.getContest()
  }
}
</script>

<style lang="less" scoped>
.add-contest,
.edit-contest {
  padding: 0 9%;
}
.el-dialog {
  .el-form-item {
    padding: 0 10%;
    .el-select {
      width: 100%;
    }
    .el-date-editor--datetimerange.el-input,
    .el-date-editor--datetimerange.el-input__inner {
      width: 100%;
    }
  }
}
</style>
