<template>
  <div>
    <div class="split-line">
      <span><i class="icon-planeo iconfont"></i></span>
    </div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-row class="mixInp" :gutter="20">
      <el-col :span="6">
        <el-input
          placeholder=""
          v-model="query.keyword"
          clearable
          @clear="getPost(selectIndex)"
        >
          <el-select
            v-model="selectIndex"
            slot="prepend"
            placeholder=""
            class="input-with-select"
            style="width: 100px"
          >
            <el-option label="" :value="1"></el-option>
            <el-option label="ID" :value="2"></el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getPost(selectIndex)"
          ></el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-table stripe style="width: 100%" :data="postList">
      <el-table-column prop="title" label="" min-width="180">
      </el-table-column>
      <el-table-column prop="authorId" label=""> </el-table-column>
      <el-table-column prop="createTime" label="" min-width="120">
        <template v-slot="{ row }">
          {{ row.createTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="" min-width="120">
        <template v-slot="{ row }">
          {{ row.updateTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column
        prop="lastReplyTime"
        label=""
        min-width="120"
      >
        <template v-slot="{ row }">
          {{ row.lastReplyTime | formatDate }}
        </template>
      </el-table-column>
      <el-table-column prop="replyNum" label="" min-width="80">
        <template v-slot:default="scope">
          <el-tag
            effect="plain"
            type="info"
            :class="scope.row.replyNum > 0 ? 'myNum' : ''"
          >
            {{ scope.row.replyNum }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="80">
        <template v-slot="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deletePostDialog(scope.row.id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="query.page"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="query.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      postList: [],
      query: {
        keyword: null,
        page: 1,
        size: 10
      },
      total: 0,
      selectIndex: 1
    }
  },
  created() {
    this.getPost()
  },
  methods: {
    // 
    async getPost(flag = 1) {
      if (flag === 1) {
        this.handleGetPost('/post/api/pagePosts')
      } else {
        this.handleGetPost('/post/api/pagePostByAuthorId')
      }
    },
    // 
    async handleGetPost(url) {
      const { data, status } = await this.$http.get(url, {
        params: this.query
      })
      if (status === 200) {
        const { list, pageNum: page, total } = data
        Object.assign(this.query, { page })
        this.postList = list
        this.total = total
      } else {
        this.$message.warning('')
      }
    },
    getAuthorById() {},
    // 
    handleSizeChange(size) {
      this.query.size = size
      this.query.page = 1
      this.getPost()
    },
    // 
    handleCurrentChange(currentPage) {
      this.query.page = currentPage
      this.getPost()
    },
    // 
    deletePostDialog(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error',
        center: true
      })
        .then(async () => {
          const { status, data } = await this.$http.delete(
            '/post/api/deletePost/' + id
          )
          if (status === 200) {
            if (data.success) {
              this.$message.success('')
              this.getPost(this.selectIndex)
            } else this.$message.error('')
          } else {
            this.$message.warning('')
          }
        })
        .catch(() => {
          this.$message.warning('')
        })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
